import React, { Component } from "react";

export default class Quick extends Component {

  componentDidMount() {
    this.setState({
      ctrs: this.props.ctrs,
    });
  }
  UNSAFE_componentWillReceiveProps(nextProps, nextState) {
    console.log("quick page control changed",nextProps.ctrs);
    this.setState({
      ctrs: nextProps.ctrs,
    });
  }
  onInputBlurHandler(node, e) {
    let val = e.target.value;
    // console.log(node, val);
    if (node) {
      node.getElementsByClassName("hxiong-field-value")[0].innerText = val;
    }
  }
  render() {
    let { ctrs } = this.state;
    return (
      <div className="properties quick-box">
        <p>快速填写</p>
        <div className="main">
          <table className="form">
            <tbody>
              {ctrs &&
                ctrs.map((v, i) => {
                  return (
                    <tr key={v.nodeId}>
                      <td className="title">
                        <span>{v.name}</span>
                      </td>
                      <td className="content">
                        <input
                          type="text"
                          id={v.nodeId}
                          value={v.value}
                          placeholder={v.name}
                          className="input-clearable"
                          disabled={v.sourceId ? "disabled" : ""}
                          onBlur={(e) => {
                            this.onInputBlurHandler(v.target, e);
                          }}
                        />
                      </td>
                    </tr>
                  );
                })}
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}



