/** 对Date的扩展，将 Date 转化为指定格式的String
 * 月(M)、日(d)、小时(h)、分(m)、秒(s)、季度(q) 可以用 1-2 个占位符，
 * 年(y)可以用 1-4 个占位符，毫秒(S)只能用 1 个占位符(是 1-3 位的数字)
 * 例子：
 * (new Date()).Format("yyyy-MM-dd HH:mm:ss.S") ==> 2006-07-02 08:09:04.423
 * (new Date()).Format("yyyy-M-d H:m:s.S")      ==> 2006-7-2 8:9:4.18
 */
window.prevSelectedTd = null;

Date.prototype.format = function (fmt) {
	//author: meizz
	var o = {
		"M+": this.getMonth() + 1, //月份
		"d+": this.getDate(), //日
		"H+": this.getHours(), //小时
		"m+": this.getMinutes(), //分
		"s+": this.getSeconds(), //秒
		"q+": Math.floor((this.getMonth() + 3) / 3), //季度
		S: this.getMilliseconds(), //毫秒
	};
	if (/(y+)/.test(fmt))
		fmt = fmt.replace(
			RegExp.$1,
			(this.getFullYear() + "").substr(4 - RegExp.$1.length)
		);
	for (var k in o)
		if (new RegExp("(" + k + ")").test(fmt))
			fmt = fmt.replace(
				RegExp.$1,
				RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
			);
	return fmt;
};

var typeNodeSelected = new CustomEvent("typeNodeSelected", {
	detail: {
		typeId: "",
		selected: false,
		children: null,
	},
});

var layoutChanged = new CustomEvent("layoutChanged", {
	detail: {
		direction: "vertical" // "horizontal",
	},
});

function isParentTarget(target, attr) {
	
	var hxplugin = target.getAttribute(attr);
	if (hxplugin) {
		return target;
	}
	while (
		target.parentNode &&
		(target.getAttribute("class") || "").indexOf("hxiong-field") > -1
	) {
		target = target.parentNode;
		if (target.getAttribute(attr)) {
			return target;
		}
	}
	return false;
}
function findParentByClassName(target, c) {
	while (target) {
		if(target.tagName == "BODY") {
			return false;
		}
		if (target.className && target.className.indexOf(c) > -1) {
			return target;
		}
		target = target.parentNode;
	}
	return false;
}
function getUid() {
	var s = [];
	var hexDigits = "0123456789abcdef";
	for (var i = 0; i < 36; i++) {
		s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
	}
	s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
	s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
	s[8] = s[13] = s[18] = s[23] = "-";

	var uuid = s.join("");
	return uuid;
}
function $fireEvent(name, args) {
	if (window.dispatchEvent) {
		window.dispatchEvent(
			new CustomEvent(name, {
				detail: args
			})
		);
	} else {
		window.fireEvent(
			new CustomEvent(name, {
				detail: args,
			})
		);
	}
}

