window.addEventListener("togglePageNumber", function () {
	// console.log('toggle page number');

	var pageNums = uDocument.body.getElementsByClassName("ueditor-pagenumber");
	if(pageNums && pageNums.length > 0) {
		for(var i=0,p;p=pageNums[i];i++) {
			uDocument.body.removeChild(p);
		}
	}else {
		var height = uDocument.body.height || uDocument.body.offsetHeight;
		var pageCount = parseInt((height+950) / 975);
		// console.log("page count", pageCount);
		for(var i = 1, y; y = i*975,i <= pageCount; i++) {
			var pageNum = uDocument.createElement("div");
			pageNum.className = "ueditor-pagenumber";
			pageNum.style.position = "absolute";
			pageNum.style.top = (y-18)+"px";
			pageNum.style.left = 0;
			pageNum.innerHTML = "第"+i+"页/共"+pageCount+"页";
			uDocument.body.appendChild(pageNum);
		}
	}
});
window.addEventListener('tableChanged', function(e){
	var {table, cell} = e.detail;
	var UT = UE.UETable, utable,ut;

	if(!table){
		return;
	}
	//修改表格样式
	utable = uDocument.getElementById(table.nodeId);
	if(!utable) return;
	ut = UT.getUETable(utable);
	
	var tableStyle= {

	}
	if(table.borderColor && table.borderWidth) {
		tableStyle.borderColor = table.borderColor;
		tableStyle.borderStyle = table.borderStyle || "solid";
		tableStyle.borderWidth = table.borderWidth + "px";
	}
	if(table.bgColor) {
		tableStyle.backgroundColor = table.bgColor;
	}
	ut.setTableStyle(tableStyle);

	utable.setAttribute("obj",JSON.stringify(table).replace(/\"/g,'\''));
	ut.update();
})
