import React, { Component } from "react";
import {req} from "./ueditor.plugin.request";

export default class UserControlBox extends Component {
  state = {
    controls: []
  }
  componentDidMount() {
    this.loadControl();
  }

  loadControl() {
    var that = this;
    req.getControlList().then(res=>{
      that.setState({
        controls: res,
      });
    }).catch(err=>console.error(err));
  }

  controlItemHandler(e, command, attr) {
    console.log(command, attr);
    window.editor.execCommand(command, attr);
  }

  hideControlHandler() {
    this.props.hideControlBox();
  }

  render() {
    let { controls } = this.state;
    return (
      <div className="user-control-box">
        <div>
          常用组件
          <div
            className="user-control-box-icon"
            onClick={this.hideControlHandler.bind(this)}
          ></div>
        </div>
        <div>
          <ul>
            {controls.map((v, i) => {
              let icon = "user-control-box-icon-" + v.typeCommand;
              return (
                <li
                  key={i}
                  command={v.typeCommand}
                  onDoubleClick={(e) => {
                    this.controlItemHandler(e, v.typeCommand, v);
                  }}
                >
                  <div className="user-control-box-item">
                    <div className={icon} />
                    {v.name}
                  </div>
                </li>
              );
            })}
          </ul>
        </div>
      </div>
    );
  }
}



