import React,{Component} from 'react'
import Ueditor from './ueditor';

export default class Gloves extends Component {

  id = "ueditor";

  render () {
    let {style} = this.props;
    return (
      <div className="emra-hx">
        <Ueditor id={this.id} style={style} />
      </div>
    );
  }
}



