/**
 * @method 判断该节点或者所有父节点中是否包含hxplugin属性,有则返回节点,没有返回fasle
 * 元素有hxiong-field属性才判断,为减少判断次数
 * @param target
 * @param parent
 * @return
 */


function isParentTarget(target, attr) {
  var isHXField =
    (target.getAttribute("class") || "").indexOf("hxiong-field") > -1;
  var hxplugin = target.getAttribute(attr);
  if (isHXField) {
    if (hxplugin) {
      return target;
    } else {
      while (
        target.parentNode &&
        (target.getAttribute("class") || "").indexOf("hxiong-field") > -1
      ) {
        target = target.parentNode;
        if (target.getAttribute(attr)) {
          return target;
          break;
        }
      }
    }
  } else {
    return false;
  }
}

function parseValue(val) {
  if (val && Array.isArray(val)) {
    return val.join(",");
  }
  return val || "";
}

function goto(param, url, newTab) {
  newTab = newTab || false;
  url = (url ? url : "/index.html") + (param ? $makeQuery(param) : "");
  if (newTab == false) {
    location.href = url;
  } else {
    window.open(url, "_blank");
  }
}

/**
 * @method 获取target元素所在iframe下的body元素
 * @param target
 * @return
 */
function getBodyTarget(target) {
  if (target.tagName == "BODY") {
    return target;
  }
  while ((target = target.parentNode)) {
    if (target.tagName == "BODY") {
      return target;
    }
  }
}
function getUid() {
  var s = [];
  var hexDigits = "0123456789abcdef";
  for (var i = 0; i < 36; i++) {
    s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
  }
  s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
  s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
  s[8] = s[13] = s[18] = s[23] = "-";

  var uuid = s.join("");
  return uuid;
}

function $makeQuery(queryObject) {
  const query = Object.entries(queryObject)
    .reduce((result, entry) => {
      result.push(entry.join("="));
      return result;
    }, [])
    .join("&");
  return `?${query}`;
}

function $format(d, fmt) {
  //author: meizz
  var o = {
    "M+": d.getMonth() + 1, //月份
    "d+": d.getDate(), //日
    "H+": d.getHours(), //小时
    "m+": d.getMinutes(), //分
    "s+": d.getSeconds(), //秒
    "q+": Math.floor((d.getMonth() + 3) / 3), //季度
    "S": d.getMilliseconds(), //毫秒
  };
  if (/(y+)/.test(fmt))
    fmt = fmt.replace(
      RegExp.$1,
      (d.getFullYear() + "").substr(4 - RegExp.$1.length)
    );
  for (var k in o)
    if (new RegExp("(" + k + ")").test(fmt))
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length)
      );
  return fmt;
};

function $alert(message) {
  const op = {
    title: "提示",
    message: message || "你想要干什么哦？",
  };
  let param = "";
  if (op.title && op.message) {
    param = $makeQuery({ title: op.title, message: op.message });
  }
  let url =
    window.editor.options.UEDITOR_HOME_URL +
    "dialogs/extends/alert.html" +
    param;
  console.log("dialog url", url);
  var dialog = new UE.ui.Dialog({
    //弹出模式以iframe方式打开的控件配置页面 URL
    iframeUrl: url,
    name: getUid(),
    editor: window.editor,
    title: op.title, //弹出框标题
    cssRules: "width:" + 300 + "px;height:" + 90 + "px;",
    buttons: [
      //弹出框按钮集
      {
        className: "edui-okbutton",
        label: "确定",
        onclick: function () {
          dialog.close(true);
        },
      },
    ],
  });
  dialog.render();
  dialog.open();

  return dialog;
}

function getRadioOptionHtml(name, align, options) {
  let html = "";
  for (var i = 0; i < options.length; i++) {
    var sChecked = "";
    if (options[i].selected) {
      sChecked = "checked";
    }
    html +=
      '<span class="hxiong-field pretty inline"><input class="hxiong-field" name="' +
      name +
      '" value="' +
      options[i].value +
      '" ' +
      sChecked +
      ' type="radio"/><label><i class="mdi mdi-check"></i>' +
      options[i].label +
      "</label></span>";
    if (align == "vertical") {
      //竖排
      html += "<br/>";
    }
  }
  return html;
}

