var utils = baidu.editor.utils;
var commands = [];

/**
 * 创建HXEditor对象
 * @param {JSON} options
 */
var HXEditor = function (options) {
  this.editor = options.editor; //百度编辑器实例，必须传入
  this.editorui = baidu.editor.ui;
  this.command = options.command || command;
};
/**
 * 添加原型方法
 */
HXEditor.prototype = {
  /**
   * 扩展编辑器命令
   * @param name
   * @param obj
   */
  registerCommand: function (name, obj) {
    this.editor.command[name] = obj;
  },
  /**
   * 生成命令按钮html
   */
  renderCommand: function (command) {
    var html = "";
    command = command || this.command;
    for (var i = 0; i < command.length; i++) {
      var commondUI = this.editorui[command[i]];
      if (!commondUI) {
        html += "<div class='edui-box edui-separator edui-default'></div>";
      } else {
        var ui = commondUI(this.editor);
        html += ui.renderHtml();
      }

      //ui.postRender();
    }
    return html;
  },
};

function command(editor) {
  var editH = new HXEditor({ editor: editor, command: window.editCmds });
  var fixedH = new HXEditor({ editor: editor, command: window.fixedCmds });
  var insertH = new HXEditor({ editor: editor, command: window.insertCmds });
  var emreH = new HXEditor({ editor: editor, command: window.emreCmds });
  var layoutH = new HXEditor({ editor: editor, command: window.layoutCmds });
  var fileH = new HXEditor({ editor: editor, command: window.fileCmds });
  var patientH = new HXEditor({ editor: editor, command: window.patientCms });
  var modeH = new HXEditor({ editor: editor, command: window.modeCmds });
  return {
    editHtml: editH.renderCommand(),
    fixedHtml: fixedH.renderCommand(),
    insertHtml: insertH.renderCommand(),
    emreHtml: emreH.renderCommand(),
    fileHtml: fileH.renderCommand(),
    layoutHtml: layoutH.renderCommand(),
    patientHtml: patientH.renderCommand(),
    modeHtml: modeH.renderCommand(),
  };
}

window.command = command;

