/**
 * 纵向布局
 * @param {*} editor 
 * @param {*} list 
 * @param {*} title 
 */
baidu.editor.ui.vertical = function (editor, list, title) {
  
  editor.ready(function () {
    UE.utils.cssRule(
      "kfformula",
      "img.kfformula{vertical-align: middle;}",
      editor.document
    );
  });

  var kfBtn = new UE.ui.Button({
    label: "纵向",
    name: "vertical-1",
    title: "纵向",
    //需要添加的额外样式，指定icon图标
    className: "edui-for-vertical-1",
    // cssRules: 'background-image: url("' + iconUrl + '") !important',
    onclick: function () {
      $fireEvent("layoutChanged", { direction: "vertical" });
    },
  });

  //当点到编辑内容上时，按钮要做的状态反射
  window.addEventListener("layoutChanged", function (e) {
    if (e.detail.direction == "vertical") {
      kfBtn.setDisabled(true);
    } else {
      kfBtn.setDisabled(false);
    }
  })
  return kfBtn;
};
