/**
 * 单行文本输入框控件,快速建立模式
 */
UE.plugins["input-quick"] = function () {
  var me = this,
    thePlugins = "input-quick";
  me.commands[thePlugins] = {
    execCommand: function (command, obj) {

      var nodeId = getUid();
      obj.nodeName = obj.name;
      obj.nodeId = nodeId;

      var gTitle = obj.name;

      var objStr = JSON.stringify(obj).replace(/\"/g,'\'');
      var placeStyle = 'width:auto'; //防止ueditor改变样式
      var style = 'min-height:16px;min-width:'+obj.width+'px;font-size:'+obj.fontSize+'px;color:'+obj.color+';text-align:'+obj.textAlign;

      let sourceAttr = "";

      if(obj.sourceId) {
        sourceAttr = 'sourceId="'+obj.sourceId+'"';
      }

      try {
        //{|- 使用边界，防止用户删除了span标签，程序处理要把边界替换为空
        var html = '<span class="hxiong-field hxplugin" style="'+placeStyle+'"  hxplugin="input" id="'+obj.nodeId+'" title="'+gTitle+'" obj="'+objStr+'" contenteditable="false">';
        html += '<span class="hxiong-field hx-left" style="'+placeStyle+'" contenteditable="false">&#8203;[</span>'
        /**window.getSelection().focusOffset contenteditable=true的元素获取光标位置*****/
        html +='<span class="hxiong-field hxiong-field-value" '+sourceAttr+' style="'+style+'" regex="'+obj.regex+'" contenteditable="false" onkeydown="if(window.getSelection().focusOffset==0){if(event.keyCode==8||event.keyCode==46 ||event.keyCode==10){return false;}}" onblur="this.setAttribute(\'contenteditable\',false)" onclick="if(window.mode>0&&!this.hasAttribute(\'sourceId\'))this.setAttribute(\'contenteditable\',true);this.focus();" placeholder="请输入">'+obj.defaultValue+'</span>';
        html += '<span class="hxiong-field hx-right" style="'+placeStyle+'" contenteditable="false">&#8203;]</span>'
        html +='</span>';
        editor.execCommand('insertHtml',html);
        return true ;
      } catch ( e ) {
        console.error(e);
        return false;
      }
    },
  };
};

