/**
 * select下拉框输入框控件
 */
UE.plugins["select"] = function () {
  var me = this,
    thePlugins = "select";
  me.commands[thePlugins] = {
    execCommand: function () {
      var dialog = new UE.ui.Dialog({
        //弹出模式以iframe方式打开的控件配置页面 URL
        iframeUrl:
          this.options.UEDITOR_HOME_URL + "dialogs/extends/select.html",
        name: thePlugins,
        editor: this,
        title: "插入下拉框", //弹出框标题
        cssRules: "width:600px;height:400px;",
        buttons: [
          //弹出框按钮集
          {
            className: "edui-okbutton",
            label: "确定",
            onclick: function () {
              dialog.close(true);
            },
          },
          {
            className: "edui-cancelbutton",
            label: "取消",
            onclick: function () {
              dialog.close(false);
            },
          },
        ],
      });
      dialog.render();
      dialog.open();
    },
  };
  var popup = new baidu.editor.ui.Popup({
    editor: this,
    content: "",
    className: "edui-bubble",
    _edittext: function () {
      baidu.editor.plugins[thePlugins].editdom = popup.anchorEl;
      me.execCommand(thePlugins);
      this.hide();
    },
    _delete: function () {
      if (window.confirm("确认删除该控件吗？")) {
        baidu.editor.dom.domUtils.remove(this.anchorEl, false);
      }
      this.hide();
    },
  });
  popup.render();

  /**
   * 绑定鼠标经过控件
   * 获取当前所处模式 ['设计模式','编辑模式','只读模式','审阅模式']
   * 设计模式下才显示popup
   **/
  me.addListener("mouseover", function (t, evt) {
    var pattern = me.body.getAttribute("pattern"); //获取当前模式
    if (pattern && pattern != "design") {
      return;
    }
    evt = evt || window.event;
    var el = evt.target || evt.srcElement;
    var target = isParentTarget(el, "hxplugin");
    var parentSelect = isParentTarget(el, "data-type");
    if (!target || parentSelect) {
      popup.hide();
      return;
    }
    var hxplugin = target.getAttribute("hxplugin");
    if (hxplugin == thePlugins && !parentSelect) {
      var html = popup.formatHtml(
        "<nobr>" +
          target.getAttribute("title") +
          '   下拉框: <span onclick=$$._edittext() class="edui-clickable">编辑</span>  <span onclick=$$._delete() class="edui-clickable">删除</span></nobr>'
      );
      if (html) {
        popup.getDom("content").innerHTML = html;
        popup.anchorEl = target;
        popup.showAnchor(popup.anchorEl);
      } else {
        popup.hide();
      }
    }
  });

  //绑定鼠标单击控件  实现自定义下拉列表
  me.addListener("click", function (t, evt) {

    var pattern = me.body.getAttribute("pattern"); //获取当前模式
    if (pattern && pattern == "readonly") {
      return;
    }
    evt = evt || window.event;
    var el = evt.target || evt.srcElement;
    var target = isParentTarget(el, "hxplugin");
    var parentSelect = isParentTarget(el, "data-type");
    //隐藏所有下拉选框
    var body = getBodyTarget(el);
    try {
      var HXSelect = body.getElementsByClassName("hx-select-root");
      for (var i = 0; i < HXSelect.length; i++) {
        HXSelect[i].style.display = "none";
      }
    } catch (err) {
      console.log('what is', err)
    }

    if (!target) {
      // console.log('target is null');
      return;
    }

    var model = eval("(" + target.getAttribute("obj") + ")") || {};

    var selectValue = model.defaultValue;
    var options = model.options || [];
    if (model.type != "select" || options.length == 0) {
      return;
    }
    let root = target.getElementsByClassName("hx-select-root");

    if (!root || root.length == 0) {
      var selectEl = document.createElement("span");
      selectEl.className = "hxiong-field hx-select-root";
      var html =
        '<span class="hx-select-content hxiong-field"><span class="hx-select hxiong-field" data-type="select">'; //生成html
      for (var i = 0; i < options.length; i++) {
        html +=
          '<span id="' +
          getUid() +
          '" class="' +
          (options[i].selected
            ? "selected hxiong-field li"
            : "hxiong-field li") +
          '" value="' +
          options[i].value +
          '" title="' +
          options[i].label +
          '">' +
          options[i].label +
          "</span>";
      }
      html += "</span></span>";
      selectEl.innerHTML = html;
      //selectEl.style.display = 'inline';
      target.appendChild(selectEl);
    } else {
      target.getElementsByClassName("hx-select-root")[0].style.display = "";
    }
    //实现下拉选中
    if (parentSelect) {
      var allLis = parentSelect.getElementsByClassName("li");
      
      let sltVal = [];
      if (target.getAttribute("sltVal")) {
        sltVal = eval("(" + target.getAttribute("sltVal") + ")") || [];
      }

      let oldVal = [];
      for (let i = 0; i < sltVal.length; i++) {
        oldVal.push(sltVal[i]);
      }

      let elVal = el.getAttribute("title");

      if (model.mode == "multiple") {
        if (el.getAttribute("class").indexOf("selected") > -1) {
          sltVal.splice(sltVal.indexOf(elVal), 1);
          el.className = "hxiong-field li";
        } else {
          sltVal.push(elVal);
          el.className = "hxiong-field selected li";
        }
      } else {
        sltVal = [];

        if (el.getAttribute("class").indexOf("selected") > -1) {
          el.className = "hxiong-field li";
          sltVal.push("0");
        } else {
          for (var i = 0; i < allLis.length; i++) {
            allLis[i].className = "hxiong-field li";
          }
          el.className = "hxiong-field selected li";
          sltVal.push(elVal);
        }
      }
      
      selectValue = sltVal.join("、");

      console.log("select", selectValue);
      target.setAttribute("sltVal", JSON.stringify(sltVal));
      target.getElementsByClassName(
        "hxiong-field-value"
      )[0].innerHTML = selectValue;
      target.getElementsByClassName("hx-select-root")[0].style.display =
        "none";
      
      $fireEvent("onScoreGroupChanged", {
        nodeId: model.nodeId,
        obj: model,
        oldVal,
        sltVal
      });
    }
  });
};

/**
 * select下拉框输入框控件-快捷
 */
UE.plugins["select-quick"] = function () {
  var me = this,
    thePlugins = "select-quick";
  me.commands[thePlugins] = {
    execCommand: function (command, obj) {
      console.log(obj);

      var nodeId = getUid();
      var gTitle = obj.name;
      obj.type = "select";
      obj.nodeName = obj.name;
      obj.nodeId = nodeId;

      // obj.style = obj.style || "select";
      
      var objStr = JSON.stringify(obj).replace(/\"/g,"\'");
      
      var placeStyle = 'width:auto'; //防止ueditor改变样式
      console.log(objStr);
      try {
        
        if (obj.style == "select") {
          //{|- 使用边界，防止用户删除了span标签，程序处理要把边界替换为空
         let html = '<span class="hxiong-field hxplugin" style="' + placeStyle + '" hxplugin="select" id="' + nodeId + '" title="' + gTitle + '" obj="' + objStr + '" contenteditable="false">';
          html += '<span class="hxiong-field hx-left" style="' + placeStyle + '" contenteditable="false">[</span>';
          html +=
            '<span class="hxiong-field hxiong-field-value" style="color: ' +
            obj.color +
            '">' +
            obj.defaultValue +
            "</span>";
          html += '<span class="hxiong-field hx-right" style="' + placeStyle + '" contenteditable="false">]</span>';
          html += '</span>';

           editor.execCommand('insertHtml',html);//cell
        }
        if (obj.style == "cell") {
          //铺满单元格
          let html = '<span class="hxiong-field hxplugin" style="width:auto;display:inline-block;width:calc(100% - 4px);height:100%;line-height:100%;" hxplugin="select" id="' + nodeId + '" title="' + gTitle + '" obj="' + objStr + '" contenteditable="false">';
          html += '<span class="hxiong-field hxiong-field-value" style="color: ' + obj.color + ';display:inline-block;width:100%;height:100%;line-height:100%;">' + obj.defaultValue + '</span>';
          html += '</span>';
           editor.execCommand('insertHtml',html);//cell
        }
        return true ;
      } catch ( e ) {
        console.error(e);
        return false;
      }
    },
  };
  
};

