/**
 * 绑定页面数据
 */
UE.plugins["bindwidgets"] = function () {
  var me = this,
    thePlugins = "bindwidgets";
  me.commands[thePlugins] = {
    execCommand: function () {
      var $body = this.body;
      // 获取id为HXplgin的对象
      var $children = $body.getElementsByClassName("hxplugin");
      var widgets = [];
      for (var i = 0; i < $children.length; i++) {
        var obj = eval("(" + $children[i].getAttribute("obj") + ")");
        var hxplugin = $children[i].getAttribute("hxplugin");
        var value;
        if (hxplugin == "input" || hxplugin == "select") {
          var target = $children[i].getElementsByClassName(
            "hxiong-field-value"
          )[0];
          value = target.innerText;
        } else if (hxplugin == "timeinput") {
          var target = $children[i].getElementsByTagName("input")[1];
          value = target.value;
        } else if (hxplugin == "checkbox" || hxplugin == "radio") {
          var inputs = $children[i].getElementsByTagName("input");
          value = [];
          for (var j = 0; j < inputs.length; j++) {
            if (inputs[j].checked) {
              value.push(inputs[j].value);
            }
          }
        }
        widgets.push({
          name: obj.nodeName,
          nodeId: obj.nodeId,
          type: hxplugin,
          value: value,
          regex: obj.regex?obj.regex:'',
          attr: obj,
        });
      }
      // console.log(widgets);
      return widgets;
    },
  };
};

function getAllHtml(editor) {
  return editor.getContent();
}

function getAllWidgets(editor, options) {
  var $body = editor.body;
  // 获取id为HXplgin的对象
  var $children = $body.getElementsByClassName("hxplugin");
  var widgets = [];
  for (var i = 0; i < $children.length; i++) {
    var obj = eval("(" + $children[i].getAttribute("obj") + ")");

    if (!obj) {
      // console.log("null node", $children[i]);
      continue;
    }

    var hxplugin = $children[i].getAttribute("hxplugin");
    var value = "";
    if (hxplugin == "input" || hxplugin == "select" || hxplugin == "totalscore" || hxplugin == "datetime") {
      var target = $children[i].getElementsByClassName("hxiong-field-value")[0];
      if(target) value = target.innerText;
    } else if (hxplugin == "timeinput") {
      var target = $children[i].getElementsByTagName("input")[1];
      console.log(target);
      if (target && target.value) {
        value = target.value;
      }
      if (value) {
        var vInput = $children[i].getElementsByTagName("input")[0];
        vInput.value = $format(new Date(value.replace(/-/g, "/")),obj.textType);
      }
    } else if (hxplugin == "checkbox" || hxplugin == "radio") {
      var inputs = $children[i].getElementsByTagName("input");
      value = [];
      for (var j = 0; j < inputs.length; j++) {
        if (inputs[j].checked) {
          value.push(inputs[j].value);
        }
      }
    } else if (hxplugin == "score") {
      value = obj.value;
    } else if(hxplugin == "text") {
      value = $children[i].innerText.replace(/[\r\n]/g,'');
      // console.log('table text value', value);
    }
    let widget = {};
    if (options) {
      widget = {
        name: obj.nodeName,
        nodeId: obj.nodeId,
        sourceId: obj.sourceId,
        type: hxplugin,
        plan: obj.plan,
        value: value,
        regex: obj.regex ? obj.regex : "",
        attr: obj,
      };
    } else {
      widget = {
        name: obj.nodeName,
        nodeId: obj.nodeId,
        sourceId: obj.sourceId,
        type: hxplugin,
        value: value,
        plan: obj.plan,
        regex: obj.regex ? obj.regex : "",
        attr: obj,
        target: $children[i],
      };
    }
    // console.log(widget);
    widgets.push(widget);
  }
  // console.log(widgets);
  return widgets;
}

