function getDoc(editor, options) {
  const widgets = getAllWidgets(editor, options); // me.execCommand('allwidgets',{target:false});
  const html = getAllHtml(editor); // me.execCommand("allhtml");

  var d = JSON.parse(localStorage.getItem("doc"));
  console.log("do save", d);

  if (!d) {
    return null;
  }

  console.log('all widgets', widgets);

  let data = [];
  let attrs = [];

  for (let i = 0; i < widgets.length; i++) {
    let item = widgets[i];
    let attr = {
      color: item.attr.color,
      fontSize: item.attr.fontSize,
      defaultValue: item.attr.defaultValue,
      nodeId: item.attr.nodeId,
      nodeName: item.attr.nodeName,
      regex: item.attr.regex,
      textAlign: item.attr.textAlign,
      width: item.attr.width,
      type: item.attr.type,
      style: item.attr.style,
      options: item.attr.options,
      score: item.attr.score,
      plan: item.attr.plan,
    };

    let d = {
      nodeId: item.nodeId,
      name: item.name,
      value: parseValue(item.value),
      type: item.type,
      regex: item.regex,
      sourceId: item.sourceId,
      groupName: item.attr.groupName || '',
      totalGroupName: item.attr.totalGroupName || '',
      plan: item.plan,
      // options:
      attr: attr,
    };

    data.push(d);
    attrs.push(attr);
  }

  let layout = editor.body.getAttribute("layout");

  let doc = {
    html: html,
    structure: JSON.stringify(widgets),
    data: data,
    attrs: attrs,
    layout: layout,
  };

  Object.assign(d.doc, doc);
  
  // console.log(widgets);
  // console.log(html);
  // console.log(doc);

  localStorage.setItem("doc", JSON.stringify(d));

  return d;
}

