baidu.editor.ui.save = function (editor, list, title) {

  var kfBtn = new UE.ui.Button({
    name: "save-1",
    title: "保存文档",
    //需要添加的额外样式，指定icon图标
    className: "edui-for-save-1",
    onclick: function () {
      let { doc } = getDoc(editor, { target: false });

      // console.log(doc);
      if (doc.patientId || doc.patientDocId) {
        window.req
          .addPatientDoc(function () {
            kfBtn.setDisabled(true);
            // console.log("do save patient doc");
          }, doc)
          .then((res) => {
            kfBtn.setDisabled(false);
            $alert("保存成功！");
          })
          .catch((err) => {
            kfBtn.setDisabled(false);
            $alert(err);
          });
      } else {
        window.req.addDoc(function(){
          kfBtn.setDisabled(true);
          // console.log('do save doc');
        },doc).then(res=>{
          kfBtn.setDisabled(false);
          $alert("保存成功！");
        }).catch(err => {
          kfBtn.setDisabled(false);
          $alert(err);
        });
      }
    },
  });

  
  return kfBtn;
};

