import React, { Component } from "react";

import TreeNode from "./ueditor.plugin.tree"
import "./animated-tree.scss";
import {req} from "./ueditor.plugin.request"
    
export default class DeptDocTree extends Component {
  d = [
    {
      id: 0,
      label: "1",
      children: [
        {
          id: 1,
          label: "2"
        }
      ]
    },
    {
      id: 4,
      label: "4",
      children: [
        {
          id: 5,
          label: "6"
        }
      ]
    }
  ];
  state = {
    patient: {},
    children: []
  };

  UNSAFE_componentDidMount() {

    // console.log("componentWillMount");
    let {patient,children} = this.props;
    this.setState({
      patient: this.props.patient,
      children: this.props.children || [],
    });

    this.reloadTypeList(patient, children);
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    let oldPatient = this.state.patient;

    let {patient,children} = nextProps;

    if(patient && oldPatient &&patient.patientId == oldPatient.patientId) {
      return;
    }

    // console.log("componentWillReceiveProps");

    // console.log('next props ', nextProps);
    this.setState({
      patient: patient,
      children: children,
      children: []
    })

    this.reloadTypeList(patient, children);
  }

  reloadTypeList(patient, children) {
    // console.log("reloadTypeList", patient);
    if(patient) {
      this.getDocTypeList(patient.patientId);
    }
  }

  getDocTypeList(patientId) {

    // let d = JSON.parse(sessionStorage.getItem("typelist"));
    // if(d) {
    //   this.data = d;
    //   this.setState({
    //     children: d
    //   });
    //   console.log("typelist was use local store data");
    //   return;
    // } 
    
    var that = this;
    req.getDocTypeList(patientId).then(res => {
      sessionStorage.setItem("typelist", JSON.stringify(res));
      that.data = res;

      let startIndex = 100000;
      let pdocs = res;
      // console.log(pdocs);
      for (let i = 0; i < pdocs.length; i++) {
        let p = pdocs[i];
        if (Array.isArray(p.patientDocs) && p.patientDocs.length > 0) {
          // console.log(p.children);
          let docs = p.patientDocs;
          if (!p.children) {
            p.children = [];
          }
          for (let j = 0; j < docs.length; j++) {
            docs[j].id = startIndex + 1; //p.patientDocs[j].docId;
            docs[j].label = docs[j].docName + "(V" + docs[j].docVersion + ")";
            docs[j].open = true;
            docs[j].data = docs[j];
            docs[j].docId = docs[j].patientDocId;

            p.children.push(docs[j]);
          }
        }
        p.data = p.relationDoc;
      }

      // console.log(pdocs);
      that.setState({
        children: pdocs,
      });
    })
  }

  onItemClick(item) {
    console.log("on click", item);
    
    this.props.itemClick(item);
  }
  render() {
    let { patient, children } = this.state;
    return (
      <div className="user-doctree-box">
        {/* <p>病历夹[{patient.realName}]</p> */}
        <div>
          <TreeNode
            id={"0"}
            label={React.createElement('span', null, '病历夹', React.createElement('span', { style: {color: "blue", fontWeight: 800, marginLeft: "2px"}},'[' + (patient.realName) + ']'))}
            open={true}
            children={children}
            itemClick={(item) => this.onItemClick(item)}
          ></TreeNode>
        </div>
      </div>
    );
  }
}



