const BASE_URL =
  window.BASE_URL ||
  (location.host.indexOf("80") > -1
    ? "http://" + location.hostname + ":8828"
    : "");


function getPatientDocById (data){
	return new Promise((resolve,reject) => {
		try{
			let url = BASE_URL + "/api/getPatientDocById";
			console.log('req url', url);
			UE.ajax.request(url, {
        method: "POST",
        timeout: 60 * 1000,
        async: true,
        data: data,
        dataType: "json",
        contentType: "application/json",
        onsuccess: function (xhr) {
          const r = eval("(" + xhr.responseText + ")");
          console.log(r.status);
          if (r.status != 0) {
            reject(r.msg);
          } else {
            resolve(r.data);
          }
        },
        //请求失败或者超时后的回调。
        onerror: function (xhr) {
          reject("接口服务已断开或正在重启中～");
        },
      });
		}catch(err){
			reject(err);
		}
	});
}

