const getControlList = function() {
	return new Promise((resolve,reject) => {
		try{
			let url = BASE_URL + "/api/getUserControlList";
			console.log(window.options);
			if (window.options) {
				url = window.options.baseUrl + "/getUserControlList";
			}
		
			UE.ajax.request(url, {
				method: "GET",
				timeout: 10000,
				async: true,
				onsuccess: function (xhr) {
					const r = eval("(" + xhr.responseText + ")");
					if (r.status != 0) {
						reject(r.msg);
					} else {
						resolve(r.data);
					}
				},
				//请求失败或者超时后的回调。
				onerror: function (xhr) {
					reject("接口服务已断开或正在重启中～");
				},
			});
		}catch(err){
			reject(err);
		}
	});
}

