import React, { Component } from "react";
import {format} from "./ueditor.plugin.utils"

export default class PropertiesBox extends Component {
  state = {
    type: "select",
    data: {},
    target: null,
    newOptionVal: "",
  };
  componentDidMount() {
    let { data, target } = this.props.data;
    this.setState({
      type: data ? data.type : "",
      data: data,
      target: target,
      newOptionVal: "",
    });
    console.log("shwo properties", data.type);
  }
  UNSAFE_componentWillReceiveProps(nextProps, nextState) {
    let { data, target } = nextProps.data;
    this.setState({
      type: data ? data.type : "",
      data: data,
      target: target,
      newOptionVal: "",
      nodeName: "",
    });
    console.log("shwo properties", data);
  }

  hideControlHandler() {
    this.props.hideControlBox();
  }

  onNewOptionValChanged = (e) => {
    this.setState({
      newOptionVal: e.target.value,
    });
  };

  inputValChanged = (e) => {
    // console.log(e);
    let { data } = this.state;
    let { name, value } = e.target;
    if (name) {
      this.setState({ data: { ...data, [name]: value } });
    }
  };

  maxDateHandler = (d) => {
    let value = format(d, "yyyy-MM-dd HH:mm:ss");
    let { data } = this.state;
    if (name) {
      this.setState({ data: { ...data, maxDate_1: value,maxDate_2:d } });
    }
  }

  onSaveChanged() {
    let { data, target } = this.state;
    let hata = { data: data, target: target };
    console.log(hata);
    this.props.onSaveChanged(hata);
  }

  removeOptionHandler = (i) => {
    console.log('do remove options',i);
    let { data } = this.state;
    if(data.options) {
      data.options.splice(i,1);
      this.setState({
        data: data
      })
    }
  }
  onNewOptionHandler() {
    let { newOptionVal, data } = this.state;
    console.log(newOptionVal);
    if(!newOptionVal) return;
    if (!data.options) {
      data.options = [
        {
          label: newOptionVal,
          value: newOptionVal,
          selected: false,
        },
      ];
    } else {
      data.options.push({
        label: newOptionVal,
        value: newOptionVal,
        selected: false,
      });
    }

    this.setState({
      data: data,
      newOptionVal: "",
    });
  }

  getTypeText(type) {
    if(type == 'input') {
      return '文本输入框';
    } else if(type == 'score') {
      return '记分组件';
    } else if(type == 'totalscore') {
      return '总分组件';
    } else if(type == 'radio') {
      return '单选组件';
    } else if(type == 'checkbox') {
      return '多选组件';
    } else if(type == 'select') {
      return '下拉组件';
    } else if(type == 'timeinput') {
      return '日期时间'
    }

    return '';
  }

  render() {
    let { type, data, newOptionVal, nodeName } = this.state;
    return (
      <div className="properties user-properties-box">
        <div>
          组件属性
          <div
            className="user-properties-icon"
            onClick={this.hideControlHandler.bind(this)}
          ></div>
        </div>
        <div className="properties-content">
          {type == "input" && (
            <div className="properties-item">
              <p>类型 <label className="right">{this.getTypeText(type)}</label></p>
              <p>编号 <input type="text" value={data.nodeId} readOnly></input>
              </p>
              <p>名称 
                <input
                  type="text"
                  name="nodeName"
                  value={data.nodeName}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>默认值 <input
                  type="text"
                  name="defaultValue"
                  value={data.defaultValue}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>类型 <select
                  type="text"
                  name="textType"
                  value={data.textType}
                  onChange={(event) => this.inputValChanged(event)}
                >
                  <option value="普通文本">普通文本</option>
                </select>
              </p>
              <p>正则 <input
                  type="text"
                  name="regex"
                  value={data.regex}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>宽度 <input
                  type="number"
                  name="width"
                  value={data.width}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>字体</p>
              <div style={{paddingLeft: "10px"}}>
                <p>颜色 <input
                    type="color"
                    name="color"
                    value={data.color}
                    onChange={(event) => this.inputValChanged(event)}
                  ></input>
                </p>
                <p>大小 <input
                    type="number"
                    name="fontSize"
                    value={data.fontSize}
                    onChange={(event) => this.inputValChanged(event)}
                  ></input>
                </p>
               </div>
              
              <p>方向 </p>
              <p><input
                  type="radio"
                  name="textAlign"
                  value="left"
                  checked={data.textAlign == "left" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                左
                <input
                  type="radio"
                  name="textAlign"
                  value="center"
                  checked={data.textAlign == "center" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                中
                <input
                  type="radio"
                  name="textAlign"
                  value="right"
                  checked={data.textAlign == "right" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                右
              </p>
              <p>
                数据源 <input type="text" value={data.sourceName} disabled></input>
              </p>
            </div>
          )}
          {type == "score" && (
            <div className="properties-item">
              <p>类型 <label className="right">{this.getTypeText(type)}</label></p>
              <p>编号 <input type="text" value={data.nodeId} readOnly></input>
              </p>
              <p>名称 <input
                  type="text"
                  name="nodeName"
                  value={data.nodeName}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>默认值 <input
                  type="text"
                  name="defaultValue"
                  value={data.defaultValue}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>分组 <input
                  type="text"
                  name="groupName"
                  value={data.groupName}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>总分名称 <input
                  type="text"
                  name="totalGroupName"
                  value={data.totalGroupName}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>最小宽度 <input
                  type="number"
                  name="width"
                  value={data.width}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>字体颜色 <input
                  type="color"
                  name="color"
                  value={data.color}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>字体方向</p>
              <p>
                <input
                  type="radio"
                  name="textAlign"
                  value="left"
                  checked={data.textAlign == "left" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                左
                <input
                  type="radio"
                  name="textAlign"
                  value="center"
                  checked={data.textAlign == "center" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                中
                <input
                  type="radio"
                  name="textAlign"
                  value="right"
                  checked={data.textAlign == "right" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                右
              </p>
            </div>
          )}
          {type == "totalscore" && (
            <div className="properties-item">
              <p>类型 <label className="right" >{this.getTypeText(type)} </label></p>
              <p>编号 <input type="text" value={data.nodeId} readOnly></input>
              </p>
              <p>名称 <input
                  type="text"
                  name="nodeName"
                  value={data.nodeName}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>默认值 <input
                  type="text"
                  name="defaultValue"
                  value={data.defaultValue}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>合计名称 <input
                  type="text"
                  name="totalGroupName"
                  value={data.totalGroupName}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>最小宽度 <input
                  type="number"
                  name="width"
                  value={data.width}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>字体颜色 <input
                  type="color"
                  name="color"
                  value={data.color}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>字体方向</p>
              <p>
                <input
                  type="radio"
                  name="textAlign"
                  value="left"
                  checked={data.textAlign == "left" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                左
                <input
                  type="radio"
                  name="textAlign"
                  value="center"
                  checked={data.textAlign == "center" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                中
                <input
                  type="radio"
                  name="textAlign"
                  value="right"
                  checked={data.textAlign == "right" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                右
              </p>
            </div>
          )}
          {(type == "timeinput" || type == "dattime")&& (
              <div className="properties-item">
                <p>类型 <label className="right" >{this.getTypeText(type)} </label></p>
                <p>编号 <input type="text" value={data.nodeId} readOnly></input>
                </p>
                <p>名称 <input
                    type="text"
                    name="nodeName"
                    value={data.nodeName}
                    onChange={(event) => this.inputValChanged(event)}
                  ></input>
                </p>
                <p>默认值 <input
                    type="text"
                    name="defaultValue_1"
                    value={data.defaultValue_1}
                    onChange={(event) => this.inputValChanged(event)}
                  ></input>
                </p>
                <p>最小时间 <input
                    type="text"
                    name="minDate_1"
                    value={data.minDate_1}
                    onChange={(event) => this.inputValChanged(event)}
                  ></input>
                </p>
                <p>最大时间 <input
                    type="text"
                    name="maxDate_1"
                    value={data.maxDate_1}
                    onChange={(event) => this.inputValChanged(event)}
                  ></input>
                </p>
                <p>日期格式 <input
                    type="text"
                    name="textType"
                    value={data.textType}
                    onChange={(event) => this.inputValChanged(event)}
                  ></input>
                </p>
                <p><label>字体</label></p>
                <div style={{paddingLeft: "10px"}}>
                  <p>最小宽度 <input
                      type="number"
                      name="width"
                      value={data.width}
                      onChange={(event) => this.inputValChanged(event)}
                    ></input>
                  </p>
                  <p>字体颜色 <input
                      type="color"
                      name="color"
                      value={data.color}
                      onChange={(event) => this.inputValChanged(event)}
                    ></input>
                  </p>
                </div>
                
              </div>
            )}
          {type == "select" && (
            <div  className="properties-item">
             <p>组件类型 <label className="right">{this.getTypeText(type)}</label></p>
              {/* <p>编号 <input type="text" value={data.nodeId} disabled></input> </p> */}
              <p>名称 <input
                  type="text"
                  name="nodeName"
                  value={data.nodeName}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>默认值 <input
                  type="text"
                  name="defaultValue"
                  value={data.defaultValue}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>颜色 <input
                  type="color"
                  name="color"
                  value={data.color}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>

              <p>
                数据源 <input type="text" value={data.sourceName} disabled></input>
              </p>
              <p>单选/多选</p>
              <p>
                <input
                  type="radio"
                  name="mode"
                  value="single"
                  checked={data.mode == "single" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                单选
                <input
                  type="radio"
                  name="mode"
                  value="multiple"
                  checked={data.mode == "multiple" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                多选
              </p>
              <p>总分名称 <input
                  type="text"
                  name="groupName"
                  value={data.groupName}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>样式</p>
              <p>
                <input
                  type="radio"
                  name="style"
                  checked={data.style == "select" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                下拉框
                <input
                  type="radio"
                  name="style"
                  checked={data.style == "cell" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                单元格
              </p>
              <p>固定选项</p>
              <div style={{ paddingLeft: 8 }}>
                {data.options &&
                  data.options.map((item, index) => {
                    return (
                      <div
                        key={index}
                        style={{
                          paddingLeft: 0,
                          display: "flex",
                          borderLeft: "1px dotted #5c5c5c",
                        }}
                      >
                        <div
                          style={{
                            width: 20,
                            height: 20,
                            borderBottom: "1px dotted #000000",
                          }}
                        ></div>
                        <div
                          style={{
                            marginTop: 10,
                            marginLeft: 2,
                            width: "calc(100% - 30px)",
                          }}
                        >
                          <span className="option">
                            {item.label}
                            <span className="option-icon option-icon-delete" onMouseDown={(evt)=>this.removeOptionHandler(index, evt)}></span>
                          </span>
                         
                        </div>
                      </div>
                    );
                  })}
                <div
                  style={{
                    paddingLeft: 0,
                    display: "flex",
                    borderLeft: "1px dotted #5c5c5c",
                  }}
                >
                  <div
                    style={{
                      width: 20,
                      height: 20,
                      borderBottom: "1px dotted #000000",
                    }}
                  ></div>
                  <div
                    style={{
                      marginTop: 10,
                      marginLeft: 2,
                      height: 10,
                      width: "calc(100% - 30px)",
                    }}
                  >
                    <button
                      className="right option-add"
                      onClick={this.onNewOptionHandler.bind(this)}
                    >添加</button>
                    <input
                      type="text"
                      value={newOptionVal}
                      name="newOptionVal"
                      onChange={(event) => this.onNewOptionValChanged(event)}
                    ></input>
                    {/* <a href="javascript:void()">添加</a> */}
                  </div>
                </div>
              </div>
            </div>
          )}
          {type == "radio" && (
            <div className="properties-item">
              <p>组件类型 <label className="right">{this.getTypeText(type)}</label></p>
              {/* <p>编号 <input type="text" value={data.nodeId} disabled></input> </p> */}
              <p>名称 <input
                  type="text"
                  name="nodeName"
                  value={data.nodeName}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>
              </p>
              <p>布局</p>
              <p>
                <input
                  type="radio"
                  name="textAlign"
                  value="horizontal"
                  checked={data.textAlign == "horizontal" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                水平
                <input
                  type="radio"
                  name="textAlign"
                  value="vertical"
                  checked={data.textAlign == "vertical" ? "checked" : ""}
                  onChange={(event) => this.inputValChanged(event)}
                ></input>{" "}
                垂直
              </p>
              <p>固定选项</p>
              <div style={{ paddingLeft: 8 }}>
                {data.options &&
                  data.options.map((item, index) => {
                    return (
                      <div
                        key={index}
                        style={{
                          paddingLeft: 0,
                          display: "flex",
                          borderLeft: "1px dotted #5c5c5c",
                        }}
                      >
                        <div
                          style={{
                            width: 20,
                            height: 20,
                            borderBottom: "1px dotted #000000",
                          }}
                        ></div>
                        <div
                          style={{
                            marginTop: 10,
                            marginLeft: 2,
                            width: "calc(100% - 30px)",
                          }}
                        >
                          <span className="option">
                            {item.label}
                            <span className="option-icon option-icon-delete" onMouseDown={(evt)=>this.removeOptionHandler(index, evt)}></span>
                          </span>
                         
                        </div>
                      </div>
                    );
                  })}
                <div
                  style={{
                    paddingLeft: 0,
                    display: "flex",
                    borderLeft: "1px dotted #5c5c5c",
                  }}
                >
                  <div
                    style={{
                      width: 20,
                      height: 20,
                      borderBottom: "1px dotted #000000",
                    }}
                  ></div>
                  <div
                    style={{
                      marginTop: 10,
                      marginLeft: 2,
                      height: 10,
                      width: "calc(100% - 30px)",
                    }}
                  >
                     <button
                      className="right option-add"
                      onClick={this.onNewOptionHandler.bind(this)}
                    >添加</button>
                    <input
                      type="text"
                      value={newOptionVal}
                      name="newOptionVal"
                      onChange={(event) => this.onNewOptionValChanged(event)}
                    ></input>
                    {/* <a href="javascript:void()">添加</a> */}
                  </div>
                </div>
              </div>
            </div>
          )}
          <div>
            <button
              id="search"
              className="small-x btn btn-default option-save"
              onClick={this.onSaveChanged.bind(this)}
            >
              保存
            </button>
          </div>
        </div>
      </div>
    );
  }
}



