import React, { Component } from "react";

import Tree from "./react-animated-tree.es"; // "react-animated-tree";
import "./animated-tree.scss";

const treeStyles = {
  
};

const typeStyles = {
  fontSize: "2em",
  verticalAlign: "middle",
};

export default class TreeNode extends Component {
  componentDidMount() {
    // console.log("TreeNode", this.props.children);
  }
  itemClick(item) {
    // console.log("item clicked")
    this.props.itemClick(item);
  }
  render() {
    const { id,data, label, open, children, patient } = this.props;
    return (
      <Tree
        id={id}
        content={label}
        open={open}
        data={data}
        style={treeStyles}
        type={patient && <span>[{patient}]</span>}
        onClick={(e) => this.itemClick(e)}
      >
        {children ? children.map((item) => {
              return (
                <TreeNode
                  key={item.id}
                  id={item.typeId || item.docId}
                  label={item.label}
                  data={item.data}
                  open={item.open}
                  children={item.children}
                  itemClick={(e) => this.itemClick(e)}
                ></TreeNode>
              );
            })
          : ""}
      </Tree>
    );
  }
}



